.navbar {
display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    /*position: relative;*/
    /*top: 200px;*/
    /*left:200px;*/
    /*border: 1px solid #e5e4e4;*/
    /*border-radius: 50%;*/
    /*height: 200px;*/
    /*width: 200px;*/
}

.navbar .container {
    max-width: 1240px;
    margin: auto;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
}

.navbar span {
    color: #75c7d0;
}

.navbar h1 {
    color: #bfc1ca;
}

.navbar .nav_menu {
    display: flex;
}

.navbar li {
    padding: 1rem;
}

.hamburger {
    display: none;
}

.navbar .icon {
    font-size: 1.5rem;
}

@media screen and (max-width: 940px) {
    .hamburger {
        display: block;
    }

    .navbar .nav_menu {
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        top: 90px;
        right: -100%;
        height: 100%;
        width: 100%;
        background: rgba(255, 255, 255, .98);
        transition: 0.4s ease-in-out;
        z-index: 10;
    }

    .navbar .active {
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        top: 90px;
        right: 0;
        height: 100%;
        width: 100%;
        background: rgba(255, 255, 255, .98);
        transition: 0.4s ease-in-out;
        z-index: 9;
    }

    /*.navbar .active {*/
    /*    right: 0;*/
    /*}*/

    .navbar .nav_menu li a {
        font-size: 2rem;
    }
}

.btn {
    color: #fff;
    padding: 12px 24px;
    background-image: linear-gradient(45deg, #5651e5 0%, #709dff 100%);
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    transition: .4s;
    cursor: pointer;
}

.btn:active {
    transform: translate(4px);
    box-shadow: 0px 0px 1px rgba(0, 0, 0,.2);
    border-bottom:none;
}